
.topbanner {
  background: pink;
  width: 100%;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
}
.topbanner span {
    font-size: 2rem;
    color: green;
}
.topbanner p {
    font-size: 0.66667rem;
    color: red;
}
.bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.06667rem;
  background: #09c;
  display: flex;
}
.bottom li {
    width: 33.33%;
    text-align: center;
    height: 1.06667rem;
}
.bottom li span {
      font-size: 0.46667rem;
      line-height: 0.66667rem;
}
.bottom li p {
      font-size: 0.33333rem;
}
.bottom .active {
    background: green;
    border-radius: 0.2rem;
    color: pink;
}
